import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import {NavLink } from 'react-router-dom'
import { startLogout } from '../../actions/auth';
import './navbar.css'

export const Navbar = () => {

    const dispatch = useDispatch();
    const { role, name } = useSelector( state => state.auth );

    const handleLogout = () => {
        dispatch( startLogout() );
    }

    return (
        <nav className="navbar navbar-expand-sm navbar-dark color-nav">
            <a className="navbar-brand" href="/">
                <img src='https://mas.getmati.com/static/images/mati-logo-white.svg'width="80%" height="80%" alt="Mati" />
            </a>
            <div className="navbar-collapse align-middle">
                <div className="navbar-nav align-items-center">
                    <NavLink  activeClassName="active" className="nav-item nav-link text-center ml-1" to="/docs">
                        Docs
                    </NavLink>
                    <NavLink  activeClassName="active" className="nav-item nav-link text-center ml-1" to="/sr/AD">
                        Special Rules
                    </NavLink>
                    <NavLink  activeClassName="active" className="nav-item nav-link text-center ml-1" to="/search">
                        Search
                    </NavLink>
                    {
                        (role==="Admin")&&
                        <NavLink  activeClassName="active" className="nav-item nav-link text-center ml-1" to="/users">
                        Users (Admin)
                        </NavLink>
                    }
                    {
                        (role==="Admin")&&
                        <NavLink  activeClassName="active" className="nav-item nav-link text-center ml-1"  to="/adminDoc">
                        Doc (Admin)
                        </NavLink>
                    }
                    {
                        (role==="Admin")&&
                        <NavLink  activeClassName="active" className="nav-item nav-link text-center ml-1" to="/adminSr">
                        S. R. (Admin)
                        </NavLink>
                    }
                </div>
            </div>
            <span className="navbar-brand">
                Welcome { name }
            </span>
            <button 
                className="btn btn-outline-danger"
                onClick={ handleLogout }
            >
                <i className="fas fa-sign-out-alt"></i>
                <span> Salir</span>
            </button>
        </nav>
    )
}