import React from 'react';
import { useDispatch } from 'react-redux';
import { startLogin } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';
import './login.css';

export const LoginScreen = ({history}) =>{

    const dispatch = useDispatch();

    const [ formLoginValues, handleLoginInputChange ] = useForm( {
        lEmail: '',
        lPassword:''
    } );

    const {lEmail, lPassword} = formLoginValues;
    
    const handleLogin = (e) =>{
        e.preventDefault();
        dispatch(startLogin(lEmail, lPassword));
    }
    document.body.style.backgroundColor = "#283040";
    return(
        
        <div className="container login-container mt-5  mx-auto">
                <div className="login-form-2 col-md mt-0">
                    <h3 style={{ color: '#283040' }}>Login</h3>
                    <form onSubmit={ handleLogin}>
                        <div className="form-group">
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="tucorreo@mati.io"
                                name= "lEmail"
                                value= {lEmail}
                                onChange={handleLoginInputChange}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="ContraseÃ±a"
                                name= "lPassword"
                                value= {lPassword}
                                onChange={handleLoginInputChange}
                            />
                        </div>
                        <div className="form-group offset-md-4">
                            <input 
                                type="submit"
                                className="btnSubmit"
                                value="Login" 
                            />
                        </div>
                    </form>
                </div>
        </div>
            
    )
}