import { types } from "../types/types";

const initialState = {
    modalOpenDoc: false,
    modalOpenSr: false,
}



export const uiReducer = ( state = initialState, action ) => {

    switch ( action.type ) {
        case types.uiOpenModalDoc:
            return {
                ...state,
                modalOpenDoc: true
            }

        case types.uiCloseModalDoc:
            return {
                ...state,
                modalOpenDoc: false
            }
        case types.uiOpenModalSr:
            return {
                ...state,
                modalOpenSr: true
            }

        case types.uiCloseModalSr:
            return {
                ...state,
                modalOpenSr: false
            }
        default:
            return state;
    }


}